/* Customize Login Screen */

body.login .button-primary {
  background-color: $darkOrange;
  border-color: scale-color($darkOrange, $lightness: -10%);
  text-shadow: 0 -1px 1px scale-color($darkOrange, $lightness: -15%), 1px 0 1px scale-color($darkOrange, $lightness: -15%), 0 1px 1px scale-color($darkOrange, $lightness: -15%), -1px 0 1px scale-color($darkOrange, $lightness: -15%);
  box-shadow: 0 1px 0 scale-color($darkOrange, $lightness: -20%);
}

body.login .button-primary:active,
body.login .button-primary:focus,
body.login .button-primary:hover {
  background-color: scale-color($darkOrange, $lightness: -9%);
  border-color: $darkOrange;
  text-shadow: 0 -1px 1px scale-color($darkOrange, $lightness: -20%), 1px 0 1px scale-color($darkOrange, $lightness: -20%), 0 1px 1px scale-color($darkOrange, $lightness: -20%), -1px 0 1px scale-color($darkOrange, $lightness: -20%);
  box-shadow: 0 1px 0 scale-color($darkOrange, $lightness: -25%);
}

body.login {
  background-color: $darkBlue;
}

.login h1 a {
  color: #ffffff;
  font-size: 30px;
  font-weight: 300;
  background-image: none;
  width: auto;
  height: auto;
  text-indent: 0;
}

.login #backtoblog a, .login #nav a, #nav, .privacy-policy-link {color: #ffffff;}
.login #backtoblog a:hover, .login #nav a:hover, .privacy-policy-link:hover {color: #cccccc;}
body.login .button-primary {
  background-color: $mainBlue;
  border-color: #ffffff;
  text-shadow: none;
  box-shadow: none;
}

body.login .button-primary:active, body.login .button-primary:focus, body.login .button-primary:hover {
  background-color: $darkBlue;
  border-color: #ffffff;
  text-shadow: none;
  box-shadow: none;
}

